<template>
  <div class="suduzyz">
    <Category :list="categories" :hidden="false" @search="handleSearch"></Category>
    <Info :keyword="keyword || '全部'" :page="page" :totalPage="totalPage"></Info>
    <Searcher @search="handleSearch"></Searcher>
    <Pager :page="page" :totalPage="totalPage" @changePage="handleChangePage"></Pager>
    <div class="container list">
      <div class="row">
        <div
          class="col-lg-3 col-md-4 col-sm-6 col-xs-12 card"
          v-for="(doc,index) in docs"
          :key="doc.title"
        >
          <h4>{{index+1}}</h4>
          <!-- <img class="img-fluid" :src="imgUrl+doc.title" /> -->
          <img style="width:100%" :src="imgUrl+doc.title+'?token='+token" />
          <div>【标题】：{{doc.title}}</div>
          <div>【分类】：{{doc.category}}</div>
          <div>【时间】：{{doc.updatetime}}</div>
          <div>
            【简介】：
            <span v-html="doc.intro.replace('<img','<img class=img-fluid')"></span>
          </div>
          <div class="cut">
            【xflink】：
            <a target="_blank" :href="doc.xfurl">{{doc.xfurl}}</a>
          </div>
        </div>
      </div>
    </div>
    <Searcher @search="handleSearch"></Searcher>
    <Pager :page="page" :totalPage="totalPage" @changePage="handleChangePage"></Pager>
    <Info :keyword="keyword || '全部'" :page="page" :totalPage="totalPage"></Info>
  </div>
</template>

<script>
import $ from "jquery/dist/jquery.min.js";
import Category from "./Category";
import Info from "./Info";
import Searcher from "./Searcher";
import Pager from "./Pager";
export default {
  name: "Suduzyz",
  props: {
    urlBase: String,
    token: String
  },
  components: {
    Category,
    Info,
    Searcher,
    Pager
  },
  data: function() {
    return {
      count: 0,
      keyword: "",
      page: 1,
      countPerPage: 12,
      docs: [],
      categories: []
    };
  },
  computed: {
    url: function() {
      return this.urlBase + "/suduzyz";
    },
    totalPage: function() {
      return Math.ceil(this.count / this.countPerPage);
    },
    categoryUrl: function() {
      return this.url + "/category";
    },
    countUrl: function() {
      return this.url + "/count";
    },
    imgUrl: function() {
      return this.url + "/img/";
    }
  },
  created: function() {
    this.getCategories();
    this.getCount();
    this.getContent();
  },
  methods: {
    getCategories() {
      $.get(this.categoryUrl, { token: this.token })
        .then(result => {
          if (result.success) {
            this.categories = result.categories;
          }
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    getCount() {
      $.get(`${this.countUrl}?keyword=${this.keyword}`, { token: this.token })
        .then(result => {
          if (result.success) {
            this.count = result.count;
          }
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    getContent() {
      $.get(
        `${this.url}?keyword=${this.keyword}&page=${this.page}&countPerPage=${this.countPerPage}`,
        { token: this.token }
      )
        .then(result => {
          if (result.success) {
            this.docs = result.docs;
          }
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    handleSearch(keyword) {
      this.keyword = keyword;
      this.page = 1;
      this.getCount();
      this.getContent();
      window.scrollTo(0, 0);
    },
    handleChangePage(p) {
      this.page = p;
      this.getContent();
      window.scrollTo(0, 0);
    }
  }
};
</script>

