<template>
  <div id="app">
    <Nutheader
      brand="Nutbed"
      @updateUrlBase="handleUpdateUrlBase"
      :user="username"
      @dropToken="handleDropToken"
      @getToken="handleGetToken"
      @changeSource="handleChangeSource"
    ></Nutheader>
    <Nutbody :source="username?source:'Home'" :urlBase="urlBase" :token="token"></Nutbody>
  </div>
</template>

<script>
import Nutheader from "./components/Nutheader";
import Nutbody from "./components/Nutbody";
import localforage from "localforage/dist/localforage.min.js";
import $ from "jquery/dist/jquery.min.js";
export default {
  name: "app",
  components: {
    Nutheader,
    Nutbody
  },
  data: function() {
    return {
      source: "Home",
      token: "",
      username: "",
      urlBase: ""
      // urlBase: "http://192.168.31.38:3000",
      // urlBase: "http://www.nutbrain.cn:8081",
    };
  },
  computed: {
    userUrl: function() {
      return this.urlBase + "/users";
    }
  },
  watch: {
    token: function() {
      this.getUsername();
    }
  },
  created: function() {
    localforage.getItem("token").then(token => {
      this.token = token || "";
    });
    // setInterval(this.getUsername,60*1000);
  },
  methods: {
    getUsername() {
      $.get(this.userUrl, { token: this.token }).then(result => {
        if (result.success) {
          this.username = result.username;
        } else {
          this.username = "";
        }
      });
    },
    handleChangeSource(source) {
      this.source = source;
      this.getUsername();
    },
    handleGetToken(token) {
      this.token = token;
      localforage.setItem("token", this.token);
    },
    handleDropToken() {
      this.token = "";
      localforage.setItem("token", this.token);
    },
    handleUpdateUrlBase(urlBase) {
      this.urlBase = urlBase;
    }
  }
};
</script>

