<template>
  <div class="searcher">
    <div class="input-group mb-3 justify-content-center">
      <input
        v-model="keyword"
        type="text"
        class="jump"
        placeholder="keyword"
        aria-label="keyword"
        aria-describedby="basic-addon2"
      />
      <div class="input-group-append">
        <button class="btn btn-outline-secondary" type="button" @click="handleClickSearch">搜索</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Searcher",
  props: {
  },
  data: function() {
    return {
      keyword: "",
    };
  },
  methods: {
    handleClickSearch(){
      this.$emit("search",this.keyword);
    }
  }
};
</script>

