<template>
  <div class="category">
    <ul v-if="!hidden">
      <li v-for="item in list" :key="item">
        <a href="#" @click.prevent="handleClickItem(item)">{{item}}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Category',
  props: {
    list: Array,
    hidden:Boolean
  },
  methods:{
    handleClickItem(keyword){
      this.$emit("search",keyword);
    }
  }
}
</script>

