<template>
  <div class="nrenvip">
    <button class="btn" @click="hidden=!hidden">显示演员列表</button>
    <Category :hidden="hidden" :list="actors" @search="handleSearch"></Category>
    <Info :keyword="keyword || '全部'" :page="page" :totalPage="totalPage"></Info>
    <Searcher @search="handleSearch"></Searcher>
    <Pager :page="page" :totalPage="totalPage" @changePage="handleChangePage"></Pager>
    <div class="container list">
      <div class="row">
        <div
          class="col-lg-3 col-md-4 col-sm-6 col-xs-12 card"
          v-for="(doc,index) in docs"
          :key="doc.number"
        >
          <h4>{{index+1}}</h4>
          <!-- <img class="img-fluid" :src="imgUrl+doc.number" /> -->
          <img style="width:100%" :src="imgUrl+doc.number+'?token='+token" />
          <div>演员：{{doc.name}}</div>
          <div>番号：{{doc.number}}</div>
          <button class="btn btn-info" @click="handleClickShowRes(index)">
            <span v-if="showIndex==index">收起资源</span>
            <span v-else>显示资源({{doc.resources.length || 0}})</span>
          </button>
          <div v-if="showIndex==index" class="resources">
            <ul>
              <li v-for="(res,indexR) in doc.resources" :key="res.magnetlink">
                <h5>{{indexR+1}}</h5>
                <div>标题：{{res.title}}</div>
                <div>类型：{{res.type}}</div>
                <div>创建：{{res.ctime}}</div>
                <div>大小：{{res.filesize}}</div>
                <div>热度：{{res.hot}}</div>
                <div>最近：{{res.lastd}}</div>
                <div class="link">
                  磁力：
                  <a :href="res.magnetlink">{{res.magnetlink}}</a>
                </div>
                <div class="link">
                  迅雷：
                  <a :href="res.thunderlink">{{res.thunderlink}}</a>
                </div>
              </li>
            </ul>
            <button class="btn btn-info" @click="handleClickShowRes">收起资源</button>
          </div>
        </div>
      </div>
    </div>
    <Searcher @search="handleSearch"></Searcher>
    <Pager :page="page" :totalPage="totalPage" @changePage="handleChangePage"></Pager>
    <Info :keyword="keyword || '全部'" :page="page" :totalPage="totalPage"></Info>
  </div>
</template>

<script>
import $ from "jquery/dist/jquery.min.js";
import Info from "./Info";
import Pager from "./Pager";
import Searcher from "./Searcher";
import Category from "./Category";
export default {
  name: "Nrenvip",
  components: {
    Info,
    Pager,
    Searcher,
    Category
  },
  props: {
    urlBase: String,
    token: String
  },
  computed: {
    url: function() {
      return this.urlBase + "/nrenvip";
    },
    totalPage: function() {
      return Math.ceil(this.count / this.countPerPage);
    },
    actorUrl: function() {
      return this.url + "/actors";
    },
    countUrl: function() {
      return this.url + "/count";
    },
    imgUrl: function() {
      return this.url + "/img/";
    }
  },
  data: function() {
    return {
      count: 0,
      keyword: "",
      page: 1,
      countPerPage: 12,
      docs: [],
      showIndex: null,
      actors: [],
      hidden: true
    };
  },
  created: function() {
    this.getActors();
    this.getCount();
    this.getContent();
  },
  methods: {
    getActors() {
      $.get(this.actorUrl, { token: this.token })
        .then(result => {
          if (result.success) {
            this.actors = result.actors;
          }
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    getCount() {
      $.get(`${this.countUrl}?keyword=${this.keyword}`, { token: this.token })
        .then(result => {
          if (result.success) {
            this.count = result.count;
          }
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    getContent() {
      $.get(
        `${this.url}?keyword=${this.keyword}&page=${this.page}&countPerPage=${this.countPerPage}`,
        { token: this.token }
      )
        .then(result => {
          if (result.success) {
            this.docs = result.docs;
          }
        })
        .catch(err => {
          window.console.log(err);
        });
    },
    handleClickShowRes(index) {
      window.console.log(index);
      if (this.showIndex == index) {
        this.showIndex = null;
      } else {
        this.showIndex = index;
      }
    },
    handleChangePage(p) {
      this.page = p;
      this.getContent();
      window.scrollTo(0, 0);
    },
    handleSearch(keyword) {
      this.keyword = keyword;
      this.page = 1;
      this.getCount();
      this.getContent();
      this.hidden = true;
      window.scrollTo(0, 0);
    }
  }
};
</script>

